/**
 * Commonly used constants and functions.
 *
 * @module Helpers
 */

import { Linear, TweenLite } from 'gsap';

/**
 * Cache body DOM element.
 *
 * @constant
 * @type {jQuery}
 */
export const $body = $('body');

/**
 * Cache document.
 *
 * @constant
 * @type {jQuery}
 */
export const $document = $(document);

/**
 * Cache window.
 *
 * @constant
 * @type {jQuery}
 */
export const $window = $(window);

/**
 * Cache header.
 *
 * @constant
 * @type {jQuery}
 */
export const $header = $('header');

/**
 * Cache footer.
 *
 * @constant
 * @type {jQuery}
 */
export const $footer = $('footer');

/**
 * Detect current page.
 *
 * @constant
 * @type {String}
 */
export const currentPage = $body.find('main').data('page');

/**
 * Toggle class on specified element on click.
 *
 * @param {jQuery} clickHandler
 * @param {jQuery} element
 * @param {String} [className='active']
 */
export const toggleClass = (clickHandler, element, className = css.active) => {
  clickHandler.on('click tap', () => element.toggleClass(className));
};

/**
 * Check if element is in viewport.
 *
 * @param {jQuery} $element
 * @param {Boolean} [fullyInView = false] - element should be fully in viewport?
 * @param {Number} [offsetTop = 0]
 * @returns {Boolean}
 */
export const isScrolledIntoView = ($element, offsetTop = 0, fullyInView = false) => {
  const pageTop = $window.scrollTop();
  const pageBottom = pageTop + $window.height();
  const elementTop = $element.offset().top;
  const elementBottom = elementTop + $element.height();

  if (fullyInView) return ((pageTop < elementTop) && (pageBottom > elementBottom));

  return (((elementTop + offsetTop) <= pageBottom) && (elementBottom >= pageTop));
};

/**
 * Match media device indicator.
 */
export class Resp {
  /**
   * Get window's current width.
   *
   * @get
   * @static
   * @return {Number}
   */
  static get currWidth () {
    return window.innerWidth;
  }

  /**
   * Detect touch events.
   *
   * @get
   * @static
   * @return {Boolean}
   */
  static get isTouch () {
    return 'ontouchstart' in window;
  }

  /**
   * Detect desktop device.
   *
   * @get
   * @static
   * @return {Boolean}
   */
  static get isDesk () {
    return window.matchMedia(`(min-width: 1280px)`).matches;
  }

  /**
   * Detect tablet device.
   *
   * @get
   * @static
   * @return {Boolean}
   */
  static get isTablet () {
    return window.matchMedia(`(min-width: 768px) and (max-width: 1279px)`).matches;
  }

  /**
   * Detect mobile device.
   *
   * @get
   * @static
   * @return {Boolean}
   */
  static get isMobile () {
    return window.matchMedia(`(max-width: 767px)`).matches;
  }
}

/**
 * Css class names.
 *
 * @constant
 * @type {Object}
 */
export const css = {
  active: 'active',
  open: 'open',
  finished: 'finished',
  overflow: 'overflow',
  focus: 'focus',
  error: 'has-error'
};
/**
 *
 * @param item
 * @param pageX
 * @param speedX
 * @param pageY
 * @param speedY
 */
export const setParallax = (item, pageX, speedX, pageY, speedY) => {
  TweenLite.to(item, 0.5, {
    x: (item.position().left + pageX * speedX) * 0.0008,
    y: (item.position().top + pageY * speedY) * 0.0008,
    ease: Linear.ease
  });
};



